export default {
	data() {
		return {
			loadingText: '上拉加载更多',
			id: 0,
			page: 1,
			total: 0,
			// 好评率
			good_rate: 0,
			cateIndex: 0,
			cateList: [{
					name: '全部',
					value: ''
				},
				{
					name: '好评',
					value: '/good'
				},
				{
					name: '中评',
					value: '/middle'
				},
				{
					name: '差评',
					value: '/bad'
				}
			],
			list: []
		}
	},
	filters: {
		rating(value) {
			if (value === 3) {
				return '中评'
			} else if (value > 3) {
				return '好评'
			} else {
				return '差评'
			}
		}
	},
	onLoad(e) {
		this.id = e.id
		this.__init()
	},
	onReachBottom() {
		if (this.loadingText !== '上拉加载更多') return
		this.page++
		this.loadingText !== '加载中...'
		this.getData()

	},
	onPullDownRefresh() {
		this.getData(true,() => {
			uni.showToast({
				title: '刷新成功',
				icon:'none'
			});
			uni.stopPullDownRefresh()
		})
	},

	methods: {
		async __init() {
			this.getData()
		},
		async getData(refresh = false,callback) {
			let page = refresh?1:this.page
			let res = await this.$http.get(
				`/goods/${this.id}/comments${this.cateList[this.cateIndex].value}?page=${page}`)
			this.good_rate = res.good_rate
			this.total = res.total
			this.list = !refresh ? [...this.list, ...res.list] : res.list
			this.loadingText = res.list.length < 10 ? '没有更多了' : '上拉加载更多'
			if (typeof callback === 'function') {
				callback()
			}
		},
		cateChange(index) {
			this.cateIndex = index
			uni.showLoading({
				title: '加载中...',
				mask: true
			});
			this.getData(true,() => {
				uni.hideLoading()
			})
		}
	}
}
